	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Toothy Grin","date":"01 May 2005","slug":"/2005-05-01-toothy-grin/","tags":"Y2005"},"html":"<p><a href=\"http://www.flickr.com/photos/papascott/11741568/\" title=\"Flickr\"><img src=\"http://photos8.flickr.com/11741568_42cc2e8d10_m.jpg\" border=\"0\" width=\"240\" height=\"180\" alt=\"IMG_0206\" /></a></p>\n<p>Christopher lost his first tooth this morning. He was a bit frightened at first, but some ice cream took care of that. In Germany, by the way,  you save your baby teeth in a little box <em>(Zahndose)</em> instead of selling them to the tooth fairy.</p>"},"next":{"fields":{"title":"Chain Saw Bush","date":"01 May 2005","slug":"/2005-05-01-chain-saw-bush/"}},"previous":{"fields":{"title":"Happy Birthday Dave","date":"02 May 2005","slug":"/2005-05-02-happy-birthday-dave/"}}},{"node":{"fields":{"title":"Chain Saw Bush","date":"01 May 2005","slug":"/2005-05-01-chain-saw-bush/","tags":"Y2005"},"html":"<p><a href=\"http://www.wonkette.com/politics/white-house/first-wife-swapper-laura-bush-steals-the-show-at-wh-correspondents-dinner-101728.php\" title=\"Wonkette: Laura Bush Steals the Show at WH Correspondents Dinner\">First Lady Laura Bush</a>: \"George's answer to any problem at the ranch is to cut it down with a chainsaw. Which I think is why he and Cheney and Rumsfeld get along so well.\"</p>"},"next":{"fields":{"title":"Multilateral Lasagne","date":"30 Apr 2005","slug":"/2005-04-30-multilateral-lasagne/"}},"previous":{"fields":{"title":"Toothy Grin","date":"01 May 2005","slug":"/2005-05-01-toothy-grin/"}}},{"node":{"fields":{"title":"Multilateral Lasagne","date":"30 Apr 2005","slug":"/2005-04-30-multilateral-lasagne/","tags":"Y2005"},"html":"<p>A few years ago a <a href=\"http://www.buitoni.com/\">certain brand</a> of lasagne noodles had a package in 12 languages, including the recipe for (wouldn't you know it) lasagne. The French recipe was very general; there were no measurements, just make a batch of <a href=\"http://frenchfood.about.com/od/frenchcuisinebasics/a/bechamel.htm\">Bechamel</a> sauce, then a batch of red meat sauce, etc. The German version was somewhat longer and gave exact instructions: 750 ml milk, 50 g flour, 50 g butter for the Bechamel, and so on. </p>\n<p>The English instructions were more to the point: take one can of Bechamel sauce, one can of meat sauce...</p>\n<p>Alas, the German version became our favorite lasagne recipe, but they've stopped putting it on the package. We (or at least I) keep forgetting the proportions for the Bechamel sauce. Tonight we got it right, so I'm posting this to make sure we can always <a href=\"http://www.google.com/search?q=bechamel+site%3Apapascott.de\">find it</a>.</p>"},"next":{"fields":{"title":"Lost Passport","date":"30 Apr 2005","slug":"/2005-04-30-lost-passport/"}},"previous":{"fields":{"title":"Chain Saw Bush","date":"01 May 2005","slug":"/2005-05-01-chain-saw-bush/"}}}],"pathPrefix":"","first":false,"last":false,"index":317,"pageCount":1101}}

